const { Component, render, findDOMNode, Fragment } = wp.element;
import Chart from "react-apexcharts";

const $ = jQuery;
const {
  RangeControl,
  CheckboxControl
} = wp.components;

import ReactPaginate from 'react-paginate';

import { AZON_Country_Dropdown } from './country-dropdown.class.js';

import DayPicker, { DateUtils } from 'react-day-picker';
import 'react-day-picker/lib/style.css';

import {
  WooZoneNoAWS_isset as isset,
  WooZoneNoAWS_Country_List as Country_List,
  WooZoneNoAWS_is_json as is_json,
  WooZoneMakeID as makeid,
} from "./amz.utils.js";

const alias = 'WooZone-AMZReport-'
const image_block_wrapper = document.getElementById( `${alias}wrapper` );

class WooZoneNoAWSImport extends Component {

  constructor(props) {
    super(props);

    this.DEBUG = false;

    this.settings = {
      'colors': ['#4CAF50', '#FF9800', '#b71c1c']
    };
    
    this.state = {
      is_chrome: navigator.userAgent.toLowerCase().indexOf('chrome') > -1,
      extention_loaded: false,
      not_sing_in: false,
      show_main_loader: false,
      ipc_valid: false,
      country: 'com',
      interval: '30days',
      lop_data: false,
      interval_selector: false,
      interval_selector_show: true,
      wait_extension_load: false,
      graph_interval: {
        start: this.minus_days( 30 ),
        end: this.minus_days( 0 )
      },

      series: false,

      options: {
        chart: {
          toolbar: {
            show: false
          }
        },

        colors: this.settings.colors,

        stroke: {
          show: true,
          curve: 'smooth',
          lineCap: 'butt',
          width: 2,
          dashArray: 0,      
        },

        markers: {
          size: 6
        },

        legend: {
          show: true,
          position: 'top',
          horizontalAlign: 'right'
        },

        labels: false,

        xaxis: {
          type: 'datetime'
        },

        yaxis: [
          {
            axisTicks: {
              show: true,
            },
            labels: {
              style: {
                colors: this.settings.colors[0],
              },
              formatter: function (val, opts) {
                return `$${parseFloat(val).toFixed(2)}`
              },
            },
            decimalsInFloat: 2,
            title: {
              style: {
                color: this.settings.colors[0],
              }
            },
            
          },
          {
            opposite: true,
            axisTicks: {
              show: true,
            },
            axisBorder: {
              color: this.settings.colors[1],
            },
            labels: {
              style: {
                colors: this.settings.colors[1]
              },
              formatter: function (val, opts) {
                return parseInt(val)
              },
            },
            title: {
              style: {
                color: this.settings.colors[1]
              },
              formatter: function (val, opts) {
                return parseInt(val)
              },
            },
          },
          {
            opposite: true,
            axisTicks: {
              show: true,
            },
            axisBorder: {
              color: this.settings.colors[2],
            },
            labels: {
              style: {
                colors: this.settings.colors[2]
              }
            },
            title: {
              style: {
                color: this.settings.colors[2]
              }
            },
          },
        ],
      },
    }

    this.handleDayClick = this.handleDayClick.bind(this);
    this.handleResetClick = this.handleResetClick.bind(this);

    this.filter_countries_list();
    this.force_redirect_alias();

    this.getReports = this.getData.bind( this );
  }

  // in order to direct import extension works we need to redirect with "WooZone_no_aws_keys_import" in url
  force_redirect_alias()
  {
    const redirect_hash = 'WooZone_no_aws_keys_import';

    if( window.location.hash != '#WooZone_no_aws_keys_import') {
      window.location = `${window.location}#${redirect_hash}`;
      window.location.reload();
    }
  }

  filter_countries_list()
  {
    this.countries_list = Country_List().filter( ( country ) => {
      return isset( WooZoneAmzReport.settings.AffiliateID, country.alias ) && WooZoneAmzReport.settings.AffiliateID[country.alias] != "" ? country : null
    });
  }

  custom_hash_translate( str )
  {
    if( typeof(str) == "undefined" ) return false;

    return window.atob(str).replace('#-1#', "a").replace('#-5#',"b").replace('#-6#', "c").replace('#-4#', "h").replace('#-21#', "t").replace('#-76#', "w");
  }

  getAddress()
  {
    const { country } = this.state;
    let address = 'affiliate-program.amazon.com';

    if( country == 'uk' ){ address = 'affiliate-program.amazon.co.uk'; }

    if( country == 'de' ){ address = 'partnernet.amazon.de'; }
    if( country == 'fr' ){ address = 'partenaires.amazon.fr'; }
    if( country == 'jp' ){ address = 'affiliate.amazon.co.jp'; }
    if( country == 'ca' ){ address = 'associates.amazon.ca'; }
    if( country == 'cn' ){ address = 'associates.amazon.cn'; }
    if( country == 'it' ){ address = 'programma-affiliazione.amazon.it'; }
    if( country == 'es' ){ address = 'afiliados.amazon.es'; }
    if( country == 'in' ){ address = 'affiliate-program.amazon.in'; }
    if( country == 'br' ){ address = 'associados.amazon.com.br'; }
    if( country == 'mx' ){ address = 'afiliados.amazon.com.mx'; }
    if( country == 'au' ){ address = 'affiliate-program.amazon.com.au'; }
    if( country == 'ae' ){ address = 'affiliate-program.amazon.ae'; }
    if( country == 'sg' ){ address = 'affiliate-program.amazon.sg'; }
    if( country == 'nl' ){ address = 'partnernet.amazon.nl'; }
    if( country == 'sa' ){ address = 'affiliate-program.amazon.sa'; }

    return address;
  }

  getData()
  {
    this.setState({ show_main_loader: true });

    const { graph_interval, country } = this.state;
    const { start, end } = graph_interval;
    const affID = WooZoneAmzReport.settings.AffiliateID[country];

    const request_url = `https://${ this.getAddress() }/home/reports/summary.json?query[start_date]=${ start.getFullYear() }-${ start.getMonth() + 1 }-${ start.getDate() }&query[end_date]=${ end.getFullYear() }-${ end.getMonth() + 1 }-${ end.getDate() }&query[type]=earning&query[storeId]=${ affID }&query[locale]=US&store_id=${ affID }`;

    const alias = 'WooZone-NAWS-'
    window.postMessage( {
      type: `${alias}transporter`,
      action: 'url-get-content',
      params: {
        'sub_action': 'stats_data',
        'url': request_url,
        'call_id': 'some-id',
        'delay': Math.floor(Math.random() * 50) + 1,
        'home_url': WooZoneAmzReport.validation.home_url
      }
    } );
  }

  getCustomData( data, key )
  {
    let new_arr = [];
    data.forEach( obj => {
      if( isset(obj, key ) ){
        let value = obj[key];
        if( key == 'commission_earnings' ){
          value = parseFloat( value );
        }

        new_arr.push( [ new Date(obj.day).valueOf(), value ] );
      }
    })

    return new_arr;
  }

  handleResetClick() {
    this.setState({
      'from': null,
      'to': null
    });
  }

  handleDayClick(day) 
  {
    const { graph_interval } = this.state;

    const range = DateUtils.addDayToRange(day, this.state);
    this.setState( range );

    let date_setup = 0;
    if( isset(range, 'from') && range.from != null ){
      date_setup++;
    }

    if( isset(range, 'to') && range.to != null ){
      date_setup++;
    }

    if( date_setup > 1 ){
      this.setState({
        graph_interval: { start: range.from, end: range.to }
      })
    }
  }

  getDates( startDate, stopDate ) 
  {
    var dateArray = [];
    var currentDate = moment(startDate);
    var stopDate = moment(stopDate);
    while (currentDate <= stopDate) {
        dateArray.push( moment(currentDate).format('YYYY-MM-DD') )
        currentDate = moment(currentDate).add(1, 'days');
    }
    return dateArray;
  }

  componentDidMount()
  {
    const self = this;
    const alias = 'WooZone-NAWS-'

    window.addEventListener("message", function(event) {

      if( event.data.type && (event.data.type == `${alias}response`) ) {

        const { action, sub_action, response, url } = event.data;

        console.log( action, sub_action, response, url );

        if( action == 'extension-loaded' ){

          if( isset(response, 'version') ){
            if( parseFloat(response.version) >= 2 ){
              const de_hash = self.custom_hash_translate( response.hash );

              self.setState({ 
                extention_loaded: true, 
                ipc_is_valid: de_hash == WooZoneAmzReport.validation.home_url ? true : false, 
                wait_extension_load: true 
              });
            }
          }
        }

        else if( sub_action == 'table_data' ){
          const data = JSON.parse(response.result);
          self.setState({
            table_data: data
          });
        }

        else if( sub_action == 'lop_data' ){
          const data = JSON.parse(response.result);
          self.setState({
            lop_data: data
          });
        }

        else if( sub_action == 'stats_data' ){
          
          if( is_json(response.result) == false ){
            const title = jQuery(response.result).filter('title').text();
            if( title != "undefined" ){
              self.setState({ 
                not_sing_in: true
              });
            }
          }else{
            const data = JSON.parse( response.result );

            self.setState({
              labels: self.getDates( data.query.start_date, data.query.end_date ),
              total: data.total.table,
              last_updated: data.last_updated,
              series: [{
                name: 'Earnings',
                type: 'column',
                data: self.getCustomData( data.records, 'commission_earnings' ),
              }, {
                name: 'Clicks',
                type: 'line',
                data: self.getCustomData( data.records, 'clicks' )
              }, {
                name: 'Ordered Items',
                type: 'line',
                data: self.getCustomData( data.records, 'ordered_items' )
              }]
            })
            
            // time to get the table data
            self.getTableData();

            // time to get the LOP data
            self.getLOP();
          }
        }

        else if( sub_action == 'validation' ){
            if( isset(response, 'call_id') ){
                if( response.result.status == 'invalid' ){
                  alert("Unable to validate your request. Please contact AA-Team support!");
                }

                self.setState({ show_main_loader: false });
                if( response.result.status == 'valid' ){

                  self.setState({ ipc_is_valid: true });

                  // refresh the page here!
                  window.location.reload();
                  return true;
                }
            }
          }

        if( action == 'url-get-content-response' ){
          if( sub_action == 'search' ){
            self.setState( prevState => ({
              show_main_loader: false,
              search_result_page: response.result,
              pagination: {
                ...prevState.pagination,
                ...WooZoneParser( 'paged', $.trim( $(response.result).find(".s-breadcrumb").text()), self.state.country ),
              }
            }));
          }
          else{
            if( self.DEBUG ) console.log( `Invalid sub_action: ${sub_action}` );
          }
        }
      }
    })
  }

  getTableData()
  {
    const { graph_interval, country } = this.state;
    const { start, end } = graph_interval;
    const affID = WooZoneAmzReport.settings.AffiliateID[country];

    const request_url = `https://${ this.getAddress() }/home/reports/table.json?query[type]=orders&query[start_date]=${ start.getFullYear() }-${ start.getMonth() + 1 }-${ start.getDate() }&query[end_date]=${ end.getFullYear() }-${ end.getMonth() + 1 }-${ end.getDate() }&query[tag_id]=all&query[order]=desc&query[device_type]=all&query[last_accessed_row_index]=0&query[group_by]=none&query[columns]=product_title,clicks,conversion,asin,direct_ordered_items,indirect_ordered_items,ordered_items&query[storeId]=${ affID }&query[locale]=US&query[skip]=0&query[sort]=ordered_items&query[limit]=999&store_id=${ affID }`;

    const alias = 'WooZone-NAWS-'

    window.postMessage( {
      type: `${alias}transporter`,
      action: 'url-get-content',
      params: {
        'sub_action': 'table_data',
        'url': request_url,
        'call_id': 'some-id',
        'delay': Math.floor(Math.random() * 50) + 1,
        'home_url': WooZoneAmzReport.validation.home_url
      }
    } );

    this.setState({ show_main_loader: false });
  }

  getLOP()
  {
    const { graph_interval, country } = this.state;
    const { start, end } = graph_interval;
    const affID = WooZoneAmzReport.settings.AffiliateID[country];

    const request_url = `https://${ this.getAddress() }/home/reports/table.json?query[type]=link_type&query[start_date]=${ start.getFullYear() }-${ start.getMonth() + 1 }-${ start.getDate() }&query[end_date]=${ end.getFullYear() }-${ end.getMonth() + 1 }-${ end.getDate() }&query[tag_id]=all&query[device_type]=all&query[last_accessed_row_index]=0&query[columns]=link_group_name,is_header,link_code,link_type,build_link,click_through_counts,ordered_conversion,ordered_units,shipped_units,shipped_earnings&query[storeId]=${ affID }&query[locale]=US&query[skip]=0&query[sort]=title&query[limit]=100&store_id=${ affID }`;

    const alias = 'WooZone-NAWS-'

    window.postMessage( {
      type: `${alias}transporter`,
      action: 'url-get-content',
      params: {
        'sub_action': 'lop_data',
        'url': request_url,
        'call_id': 'some-id',
        'delay': Math.floor(Math.random() * 50) + 1,
        'home_url': WooZoneAmzReport.validation.home_url
      }
    } );

    this.setState({ show_main_loader: false });
  }

  minus_days( nb )
  {
    const now = new Date();
    return new Date(now - nb * 24 * 60 * 60 * 1000);
  }

  build_chart() 
  {
    const { from, to, interval, interval_selector, total, last_updated } = this.state;
    const modifiers = { start: from, end: to };
    
    const add_symbol = ( amount, currency='US' ) => {
      if( currency == 'US' ){
        return `$${amount}`;
      }
    }

    return <div className={ `${alias}chart` }>
      <h2>Reports</h2>

      <Chart
        options={ this.state.options }
        series={ this.state.series }
        height="400"
      />
      <ul>
        <li>
          <span>Click</span>
          <span>{ total.clicks }</span>
        </li>
        <li>
          <span>Ordered Items</span>
          <span>{ total.ordered_items }</span>
        </li>
        <li>
          <span>Shipped Items</span>
          <span>{ total.shipped_items }</span>
        </li>
        <li>
          <span>Returned Items</span>
          <span>{ total.returned_items }</span>
        </li>
        <li>
          <span>Shipped Items Revenue</span>
          <span>{ add_symbol(total.revenue) }</span>
        </li>
        <li>
          <span>Total Earnings</span>
          <span>{ add_symbol(total.total_earnings_with_hva) }</span>
        </li>
      </ul>
    </div>
  }

  product_url( asin )
  {
    return `http://www.amazon.com/dp/${asin}`
  }

  build_lop_table()
  {
    const { lop_data } = this.state;

    return <div className={ `${alias}table ${alias}lop` }>
      <table>
        <thead>
          <td>Link Group</td>
          <td width="100">Clicks</td>
          <td width="100">Conversion</td>
          <td width="220">Number of items ordered.</td>
          <td width="150">Items Shipped</td>
          <td width="150">Earnings</td>
        </thead>

        <tbody>
          {
            Object.keys(lop_data.records).map( (key) => {
              const row = lop_data.records[key];
              return <tr key={ key } className={ row.is_header ? '__is_header' : '' }>
                <td>{row.link_group_name}</td>
                <td>{row.click_through_counts}</td>
                <td>{parseFloat(row.ordered_conversion).toFixed(2)}%</td>
                <td>{row.ordered_units}</td>
                <td>{row.shipped_units}</td>
                <td>${row.shipped_earnings}</td>
              </tr>
            })
          }
        </tbody>
      </table>
    </div>
  }

  build_table()
  {
    const { table_data } = this.state;

    return <div className={ `${alias}table` }>
      <table>
        <thead>
          <td width="100">Asin</td>
          <td>Product Title</td>
          <td width="100">Clicks</td>
          <td width="150">Conversion</td>
          <td width="150">Direct ordered items</td>
          <td width="180">Indirect ordered items</td>
          <td width="150">Ordered items</td>
        </thead>

        <tbody>
        {
          Object.keys(table_data.records).map( (key) => {
            const row = table_data.records[key];
            return <tr key={ key }>
              <td>{row.asin}</td>
              <td><a href={ this.product_url( row.asin ) } target="_blank">{row.product_title}</a></td>
              <td>{row.clicks}</td>
              <td>{row.conversion}</td>
              <td>{row.direct_ordered_items}</td>
              <td>{row.indirect_ordered_items}</td>
              <td>{row.ordered_items}</td>
            </tr>
          })
        }
        </tbody>
      </table>
    </div>
  }

  build_affIDs()
  {
    const { from, to, interval, interval_selector, total, last_updated, interval_selector_show, graph_interval, range, country } = this.state;
    const modifiers = { start: from, end: to };

    const monthNames = [ "January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December" ];
    const now = new Date();

    const yesterday = this.minus_days( 1 );
    const last7days = this.minus_days( 7 );
    const last30days = this.minus_days( 30 );
    const last60days = this.minus_days( 60 );

    const print_date = ( date ) => {
      return `${monthNames[date.getMonth()]} ${date.getUTCDate()} ${date.getUTCFullYear()}`;
    }
    
    const add_symbol = ( amount, currency='US' ) => {
      if( currency == 'US' ){
        return `$${amount}`;
      }
    }

    const print_current_interval = ( interval ) => {
      switch(interval) {
        case 'today':
          return `Today ${print_date( now )}`;
        case 'yesterday':
          return `Yesterday ${ print_date( yesterday ) }`;
        case '7days':
          return `Last 7 days ${ print_date( last7days ) } - ${ print_date( now ) }`;
        case '30days':
          return `Last 30 days ${ print_date( last30days ) } - ${ print_date( now ) }`;
        case '60days':
          return `Last 60 days ${ print_date( last60days ) } - ${ print_date( now ) }`;
        case 'custom':
          return `Custom date range from ${ print_date( graph_interval.start ) } to ${ print_date( graph_interval.end ) }`;
        default:
          return 'no setup ...';
      }
    }

    return <div className={ `${alias}aff-ids` }>
      <div className={ `${alias}select-country` }>
        <AZON_Country_Dropdown onChange={ (value) => {
          this.setState({
            country: value
          })

        } } defaultValue={ country } list={ this.countries_list } />
      </div>

      <div className={ `${alias}get-current-id` }>
      {
        WooZoneAmzReport.settings.AffiliateID[country]
      }
      </div>

      <div className={ `${alias}top` }>
        <div>
          <div className={ `${alias}interval-wrapper` }>
            <div className={ `${alias}select` } onClick={ e => {
              e.preventDefault();
              this.setState({
                interval_selector: !interval_selector,
                interval_selector_show: true
              });
            }}>
              { print_current_interval( interval ) } 
              <span class="dashicons dashicons-arrow-down-alt2"></span>
            </div>

            {
              interval_selector ? <ul className={ `${alias}interval` }>
                <li className={ interval == 'today' ? 'on' : '' } onClick={ e => { this.setState({ 
                  interval: 'today', 
                  interval_selector: !interval_selector,
                  graph_interval: {
                    start: this.minus_days( 0 ),
                    end: this.minus_days( 0 )
                  }
                }) }}>{ print_current_interval( 'today' ) }</li>

                <li className={ interval == 'yesterday' ? 'on' : '' } onClick={ e => { this.setState({ 
                  interval: 'yesterday', 
                  interval_selector: !interval_selector,
                  graph_interval: {
                    start: this.minus_days( 1 ),
                    end: this.minus_days( 0 )
                  }
                }) }}>{ print_current_interval( 'yesterday' ) }</li>

                <li className={ interval == '7days' ? 'on' : '' } onClick={ e => { this.setState({ 
                  interval: '7days', 
                  interval_selector: !interval_selector,
                  graph_interval: {
                    start: this.minus_days( 7 ),
                    end: this.minus_days( 0 )
                  }
                }) }}>{ print_current_interval( '7days' ) }</li>
                
                <li className={ interval == '30days' ? 'on' : '' } onClick={ e => { this.setState({ 
                  interval: '30days', 
                  interval_selector: !interval_selector,
                  graph_interval: {
                    start: this.minus_days( 30 ),
                    end: this.minus_days( 0 )
                  }
                }) }}>{ print_current_interval( '30days' ) }</li>

                <li className={ interval == '60days' ? 'on' : '' } onClick={ e => { this.setState({ 
                  interval: '60days', 
                  interval_selector: !interval_selector,
                  graph_interval: {
                    start: this.minus_days( 60 ),
                    end: this.minus_days( 0 )
                  }
                }) }}>{ print_current_interval( '60days' ) }</li>

                <li className={ interval == 'custom' ? 'on' : '' } onClick={ e => { this.setState({ interval: 'custom' }) }}>{ print_current_interval( 'custom' ) }</li>
              </ul> : ''
            }
          </div>

          {
            interval == 'custom' && interval_selector && interval_selector_show ? <div className={ `${alias}custom-range` }>
              <a href="#close" className={ `${alias}close` } onClick={ e => {
                e.preventDefault();

                this.setState({
                  interval_selector_show: false
                })
              }}><span class="dashicons dashicons-no-alt"></span></a>
              <p>
                {!from && !to && 'Please select the first day.'}
                {from && !to && 'Please select the last day.'}
                {from &&
                  to &&
                  `Selected from ${from.toLocaleDateString()} to
                      ${to.toLocaleDateString()}`}{' '}
                {from && to && (
                    <button className="link" onClick={this.handleResetClick}>
                      Reset
                    </button>
                )}
              </p>

              <DayPicker
                className="Selectable"
                numberOfMonths={ 2 }
                selectedDays={[from, { from, to }]}
                modifiers={modifiers}
                onDayClick={ this.handleDayClick }
                disabledDays={[
                  {
                    before: this.minus_days( 90 ),
                    after: new Date()
                  }
                ]}
              />
            </div> : ''
          }

        </div>
      </div>

      <div className={ `${alias}get-reports` }>
        <button onClick={ this.getReports }>get reports</button>
      </div>
    </div>
  }

  buildLoader()
  {
    return <div className={ `${alias}loader` }><div class="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div></div>
  }

  launchValidation( event )
  {
    const alias = 'WooZone-NAWS-'

    event.preventDefault();

    const ipc = $.trim(event.target.elements.ipc.value);
    const email = event.target.elements.email.value;
    const home_url = event.target.elements.home_url.value;

    let is_valid_ipc = true;
    if( ipc == '' ){
      is_valid_ipc = false;
    }

    if( is_valid_ipc == false ){
      alert("invalid IPC code!");
    }

    let validate_url = `http://cc.aa-team.com/validation/validate.php?ipc=${ipc}&home_url=${home_url}&email=${email}&app=${WooZoneAmzReport.validation.plugin_alias}-extension`;
    // debug
    //search_url = '../page2.html';

    window.postMessage( {
      type: `${alias}transporter`,
      action: 'url-get-content',
      params: {
        'sub_action': 'validation',
        'url': validate_url + `&no_cache=${ makeid() }`,
        'call_id': makeid(),
        'delay': 1,
        'home_url': WooZoneAmzReport.validation.home_url
      }
    } );

    this.setState({ show_main_loader: true });
  }

  render()
  {
    const { is_chrome, lop_data, extention_loaded, ipc_is_valid, show_main_loader, wait_extension_load, not_sing_in, series, table_data, graph_interval } = this.state;

    if( wait_extension_load == false ){
      //return <div className={ `${alias}app-loading` }>App is loading ...</div>
    }

    if( not_sing_in == true ) {
      return <div className={ `${alias}not-sing-in` }>
        <div>
          <img src={ `${WooZoneAmzReport.assets_url}images/sign.jpg` } />
          <div>
            <p>In order to get statistics you need you browser to be login into <a target="_blank" href="https://affiliate-program.amazon.com/">Amazon Associates</a> account.</p>
            <p>Dont't forget to check the "Keep me signed in" option.</p>
            <p>After you Sign-In into your account please come back in this page and just refresh the page.</p>

            <a href={ `https://${ this.getAddress() }/` } target="_blank" className={ `${alias}sign-btn` }>Sign-In</a>
          </div>
        </div>

      </div>
    }

    if( is_chrome === false ){
      return <div className={ `${alias}get-google-chrome` }>
        <img src={ `${WooZoneAmzReport.assets_url}images/chrome-logo.svg` } />
        <h1>This module works only in Google Chrome Browser and using <a href="https://chrome.google.com/webstore/detail/wzone-direct-import/gmpiiinlandbgcfejoeaodgpfkdjnolm?hl=en" target="_blank">WZone Direct Import</a>.</h1>
        <a className="_is_btn" href="https://www.google.com/chrome/" target="_blank">Download Chrome</a>
      </div>
    }

    if( extention_loaded === false ){
      return <div className={ `${alias}get-extension` }>
        <div className={ `${alias}validate-left` }>
          <p>Please check if the WZone Direct Import extension is installed!</p>
          <p className={ `${alias}info-block` }>In order to use this module please make sure you have the latest WZone Direct Import version installed - V2.0 </p>
          <p className={ `${alias}info-text` }>After you install/update it please refresh this page.</p>
          <div className={ `${alias}get-extension-block` }>
            <img src={ `${WooZoneAmzReport.assets_url}images/direct-import-logo.png` } />
            <div>
              <h4>WZone Direct Import</h4>
              <a className="_is_btn" href="https://chrome.google.com/webstore/detail/wzone-direct-import/gmpiiinlandbgcfejoeaodgpfkdjnolm?hl=en" target="_blank">Add to Chrome</a>
            </div>
          </div>
          </div>

          <div className={ `${alias}validate-right` }>
            <img src={ `${WooZoneAmzReport.assets_url}images/API-NO.jpg` } />
          </div>
      </div>
    }

    if( ipc_is_valid === false ){
      return <Fragment>
        { show_main_loader ? this.buildLoader() : '' }
        <div className={ `${alias}validate-extension` }>
        <div className={ `${alias}validate-left` }>
          <h2>WZone Direct Import Extension Validation.</h2>
          <div className={ `${alias}validate-extension-info` }>
            <p>This code is used for validation so you can use the NO AWS KEYS Import Feature.</p>
            <p>You need to log into your CodeCanyon account and go to your Downloads page. Locate the plugin you purchased in your Downloads list and click on the License Certificate link next to the download link. After you have downloaded the certificate you can open it in a text editor such as Notepad and copy the Item Purchase Code.</p>
            <p><a target="_blank" href="http://docs.aa-team.com/woocommerce-amazon-affiliates/documentation/activate-plugin/">Read more here. </a></p>
          </div>
          <form onSubmit={ this.launchValidation.bind(this) }>
            <input type="hidden" name="home_url" value={ WooZoneAmzReport.validation.home_url } />
            <input type="text" name="ipc" defaultValue={ WooZoneAmzReport.validation.ipc ? WooZoneAmzReport.validation.ipc : '' } placeholder="Enter your Envato Purchase Code here" />
            <input type="text" name="email" defaultValue={ WooZoneAmzReport.validation.email ? WooZoneAmzReport.validation.email : '' } placeholder="Enter site owner email here" />
            <button className="_is_btn" ><span class="dashicons dashicons-admin-network"></span> Unlock NO AWS KEYS Import</button>
          </form>
          </div>
          <div className={ `${alias}validate-right` }>
            <img src={ `${WooZoneAmzReport.assets_url}images/API-NO.jpg` } />
          </div>
        </div>
      </Fragment>
    }

    return <div>
      { this.build_affIDs() }

      { show_main_loader ? this.buildLoader() : '' }

      { series ? this.build_chart() : '' }
      { lop_data ? this.build_lop_table() : '' }
      { table_data ? this.build_table() : '' }
    </div>
  }
}

wp.domReady( function() {
  if( image_block_wrapper ) render( <WooZoneNoAWSImport />, image_block_wrapper );
} );
